@charset "UTF-8";
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-orange: #e07b1a;
  --color-text: var(--color-black);
  --color-borders: var(--color-black);
  --btn-text-color: var(--color-black);
  --btn-text-color--hover: var(--color-black);
  --content-max-width: min(calc(100vw - var(--container-padding)*2), 930px);
  --container-max-width: 1600px;
  --container-padding: clamp(24px, 6vw, 96px);
  --font-family-base: sans-serif;
  --line-height-base: 1.5;
  --gap: clamp(1.4375rem, 1.875vw, 1.875rem);
  --transitions: 0.3s;
  --font-size-base: 1rem;
  --font-size-h1: clamp(3.125rem, 6.25vw, 6.25rem);
  --font-size-h2: clamp(2.5rem, 5vw, 5rem);
  --font-size-h3: clamp(1.5rem, 2.5vw, 2.5rem);
  --font-size-h4: clamp(1.25rem, 2vw, 2rem);
  --font-size-h5: clamp(1.125rem, 1.5vw, 1.5rem);
  --font-size-h6: 1rem;
  --font-size-small: .875rem;
  --font-size-micro: 0.875rem;
  --spacing-0: 0;
  --spacing-1: clamp(0.3125rem, 0.625vw, 0.625rem);
  --spacing-2: clamp(0.625rem, 1.25vw, 1.25rem);
  --spacing-3: clamp(0.9375rem, 1.875vw, 1.875rem);
  --spacing-4: clamp(1.25rem, 2.5vw, 2.5rem);
  --spacing-5: clamp(1.5625rem, 3.125vw, 3.125rem);
  --spacing-6: clamp(1.875rem, 3.75vw, 3.75rem);
  --spacing-7: clamp(2.1875rem, 4.375vw, 4.375rem);
  --spacing-8: clamp(2.5rem, 5vw, 5rem);
  --spacing-9: clamp(2.8125rem, 5.625vw, 5.625rem);
  --spacing-10: clamp(3.125rem, 6.25vw, 6.25rem);
  --spacing-11: clamp(3.4375rem, 6.875vw, 6.875rem);
  --spacing-12: clamp(3.75rem, 7.5vw, 7.5rem);
  --spacing-13: clamp(4.0625rem, 8.125vw, 8.125rem);
  --spacing-14: clamp(4.375rem, 8.75vw, 8.75rem);
  --spacing-15: clamp(4.6875rem, 9.375vw, 9.375rem);
  --spacing-16: clamp(5rem, 10vw, 10rem);
  --spacing-17: clamp(5.3125rem, 10.625vw, 10.625rem);
  --spacing-18: clamp(5.625rem, 11.25vw, 11.25rem);
  --spacing-19: clamp(5.9375rem, 11.875vw, 11.875rem);
  --spacing-20: clamp(6.25rem, 12.5vw, 12.5rem);
  --site-header-height: 100px;
}

body {
  --wp--style--global--content-size: var(--content-max-width);
}

* {
  box-sizing: border-box;
}

*:focus-visible {
  outline-color: var(--color-focus-outline, var(--color-white));
  outline-style: solid;
  outline-width: 1px;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: var(--site-header-height);
}

body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: var(--font-family-base);
  font-size: var(--font-size-base);
  line-height: var(--line-height-base);
  color: var(--color-text);
  overflow-x: hidden;
}
body.no-scroll {
  overflow: hidden;
}

[href="#site-main"] {
  position: absolute;
  z-index: 1000;
}

.screen-reader-text,
.visuallyHidden,
.visuallyHidden-focusable:not(:focus):not(:focus-within) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

a {
  transition: var(--transitions);
}

button,
[type=submit] {
  appearance: none;
  background: transparent;
  border-radius: 0;
  border: 0;
  padding: 0;
  cursor: pointer;
  display: inline-block;
  font-family: inherit;
  color: currentColor;
}

figure {
  margin: 0;
}

video,
svg,
img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg *:focus {
  outline: none;
}

picture {
  display: block;
}

p,
ol,
ul,
dl,
table,
blockquote {
  margin-block: 0.75em 1.25em;
}

strong {
  font-weight: 700;
}

address {
  font-style: normal;
}

.wp-block-separator.wp-block-separator,
hr {
  margin-top: 0;
  border: 0;
  border-top: solid 1px var(--color-borders);
}

.not-found {
  padding-block: var(--spacing-10);
  text-align: center;
}

.wp-element-button,
.btn {
  appearance: none;
  background: transparent;
  border-radius: 0;
  border: 0;
  padding: 0;
  cursor: pointer;
  display: inline-block;
  font-family: inherit;
  color: currentColor;
  color: var(--btn-text-color);
}
@media (hover: hover) {
  .wp-element-button:hover,
.btn:hover {
    color: var(--btn-text-color--hover);
  }
}

.container {
  max-width: var(--container-max-width);
  padding-inline: var(--container-padding);
  margin-inline: auto;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-family-headings);
  font-style: normal;
  font-weight: 400;
  color: var(--color-headings, var(--color-text));
  text-wrap: balance;
  margin-block: 0.25em;
}
h1 strong,
h1 b,
h2 strong,
h2 b,
h3 strong,
h3 b,
h4 strong,
h4 b,
h5 strong,
h5 b,
h6 strong,
h6 b {
  font-weight: inherit;
}
h1.alignwide,
h2.alignwide,
h3.alignwide,
h4.alignwide,
h5.alignwide,
h6.alignwide {
  max-width: var(--container-max-width);
  padding-inline: var(--container-padding);
  margin-inline: auto;
  box-sizing: border-box;
}

.has-h-1-font-size,
h1 {
  font-size: var(--font-size-h1);
}

.has-h-2-font-size,
h2 {
  font-size: var(--font-size-h2);
  line-height: 1.2;
}

.has-h-3-font-size,
h3 {
  font-size: var(--font-size-h3);
}

.has-h-4-font-size,
h4 {
  font-size: var(--font-size-h4);
}

.has-h-5-font-size,
h5 {
  font-size: var(--font-size-h5);
}

.has-h-6-font-size,
h6 {
  font-size: var(--font-size-h6);
}

table {
  width: 100%;
  border-collapse: collapse;
}

.wp-block-embed {
  overflow-wrap: break-word;
}
.wp-block-embed.wp-embed-aspect-16-9 .wp-block-embed__wrapper::before {
  padding-top: 56.25%;
}
.wp-block-embed .wp-block-embed__wrapper {
  position: relative;
}
.wp-block-embed .wp-block-embed__wrapper::before {
  content: "";
  display: block;
  padding-top: 50%;
}
.wp-block-embed iframe {
  max-width: 100%;
  bottom: 0;
  height: 100%;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
}

@media (min-width: 992px) {
  #site-navigation-open {
    display: none;
  }
}

.modal,
.modal-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.modal {
  z-index: 10000;
  display: flex;
}
.modal[aria-hidden=true] {
  display: none;
}

.modal-overlay {
  background-color: rgba(0, 0, 0, 0.8);
}

.modal-content {
  margin: auto;
  z-index: 2;
  position: relative;
  background-color: var(--color-black);
  border-radius: 0.5rem;
  padding: var(--spacing-4);
}
.modal-content > button {
  position: absolute;
  right: 15px;
  top: 15px;
}

.video-modal .modal-content {
  padding: 0;
  max-height: 90vh;
}
.video-modal .modal-content button {
  right: -25px;
  top: -25px;
}
@media (hover: hover) {
  .video-modal .modal-content button:hover {
    color: var(--color-white);
  }
}
.video-modal iframe {
  display: block;
  width: min(80vw, 1000px);
  height: auto;
  aspect-ratio: 500/281;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
}
@keyframes slide-up {
  from {
    transform: translateY(10%);
  }
}
.dialog-overlay {
  animation: fade-in 200ms both;
}

/**
   * 1. Add an animation delay equal to the overlay animation duration to
   *    wait for the overlay to appear before animation in the dialog.
   */
.modal-content {
  animation: fade-in 400ms 200ms both, slide-up 400ms 200ms both;
}

fieldset,
input,
select,
textarea {
  background: transparent;
  border: 0;
  border-radius: 0;
  padding: 0;
  margin: 0;
  appearance: none;
}

input[type=text], input[type=number], input[type=email], input[type=search], input[type=url], input[type=tel] {
  font-size: 1rem;
  width: 100%;
}
input[type=radio], input[type=checkbox] {
  accent-color: var(--color-accent);
}
input[type=range] {
  width: 100%;
  accent-color: var(--color-accent);
}
input[type=submit] {
  color: var(--btn-text-color);
}
@media (hover: hover) {
  input[type=submit]:hover {
    color: var(--btn-text-color--hover);
  }
}

select {
  font-size: 1rem;
}

textarea {
  font-size: 1rem;
  height: auto;
  display: block;
  resize: vertical;
  width: 100%;
  border: solid 1px var(--color-text);
  padding: 1em;
}
textarea.small {
  height: 4em;
}

label {
  font-size: 1.25rem;
}

.gform_required_legend {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.gform_fields {
  display: grid;
  gap: 30px;
}
@media (min-width: 768px) {
  .gform_fields {
    grid-template-columns: repeat(2, 1fr);
  }
}

.gfield--width-full {
  grid-column: 1/-1;
}

.gform_footer {
  padding-top: var(--spacing-4);
}

.gform_submission_error {
  font-size: var(--font-size-base);
  padding: 1em;
  margin-bottom: 1em;
  background: rgba(255, 255, 255, 0.2);
  border: solid 1px red;
  text-align: center;
}
.gform_submission_error:focus {
  outline: none;
}

.validation_message {
  color: red;
  padding-top: 0.25em;
}

.gfield_error input::placeholder,
.gfield_error textarea::placeholder {
  color: gray;
}

/********************************
  Stuff we need from the base CSS
  ********************************/
@font-face {
  font-family: "gform-icons-theme";
  src: url("../../../../plugins/gravityforms/fonts/gform-icons-theme.woff2?a2cayk") format("woff2"), url("../../../../plugins/gravityforms/fonts/gform-icons-theme.ttf?a2cayk") format("truetype"), url("../../../../plugins/gravityforms/fonts/gform-icons-theme.woff?a2cayk") format("woff"), url("../../../../plugins/gravityforms/fonts/gform-icons-theme.svg?a2cayk#gform-icons-theme") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
/**
   Honeypot
   */
.gform_validation_container,
body .gform_wrapper .gform_validation_container,
body .gform_wrapper .gform_body .gform_fields .gfield.gform_validation_container,
body .gform_wrapper .gform_fields .gfield.gform_validation_container {
  display: none !important;
  left: -9000px;
  position: absolute !important;
}

/*
  * Hide screen reader text.
  *
  * Used in the basic.css front-end stylesheet.
  */
.gform_wrapper .screen-reader-text,
.gform_wrapper .hidden_label .gfield_label,
.gform_wrapper .hidden_sub_label {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal !important;
}

.gform_wrapper button.screen-reader-text:focus {
  border: 0;
  clip: auto;
  -webkit-clip-path: none;
  clip-path: none;
  height: auto;
  margin: 0;
  position: static !important;
  width: auto;
}

/*
  * Date field.
  *
  * Used in the basic.css front-end stylesheet and admin-theme.css.
  */
.gform-theme-datepicker:not(.gform-legacy-datepicker) {
  background: #fff;
  border: 1px solid #d0d1d3;
  border-radius: 3px;
  box-shadow: 0 0.0625rem 0.25rem rgba(0, 0, 0, 0.11), 0 0 0.25rem rgba(18, 25, 97, 0.0405344);
  color: #607382;
  font-size: 1.3rem;
  font-weight: 500;
  margin-top: 5px;
  padding: 18px 15px;
  width: auto;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) table,
.gform-theme-datepicker:not(.gform-legacy-datepicker) thead,
.gform-theme-datepicker:not(.gform-legacy-datepicker) tr,
.gform-theme-datepicker:not(.gform-legacy-datepicker) td,
.gform-theme-datepicker:not(.gform-legacy-datepicker) th {
  background: none;
  border: 0;
  margin: 0;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) td,
.gform-theme-datepicker:not(.gform-legacy-datepicker) th {
  padding: 5px;
  text-shadow: none;
  text-transform: none;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header {
  border: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 0 5px;
  padding: 0;
  position: relative;
  width: 100%;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-prev,
.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-next {
  align-items: center;
  background: none;
  border: 0;
  color: #607382;
  cursor: pointer;
  display: flex;
  font-family: gform-icons-theme !important;
  font-size: 24px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 30px;
  justify-content: center;
  line-height: 1;
  position: absolute;
  speak: never;
  text-decoration: none;
  top: -2px;
  transition: color 300ms ease-in-out, background-color 300ms ease-in-out, border-color 300ms ease-in-out;
  width: 30px;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-prev:focus,
.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-prev:hover,
.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-next:focus,
.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-next:hover {
  color: #2f4054;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-prev .ui-icon,
.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-next .ui-icon {
  display: none;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-prev::before,
.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-next::before {
  border: 0;
  height: auto;
  position: static;
  transform: none;
  width: auto;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-prev {
  left: 0;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-prev::before {
  content: "";
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-next {
  right: 0;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header .ui-datepicker-next::before {
  content: "";
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header select {
  -webkit-appearance: none;
  appearance: none;
  background-color: transparent;
  background-image: url("../../../../plugins/gravityforms/images/theme/down-arrow.svg");
  background-position: 100% 50%;
  background-repeat: no-repeat;
  background-size: 24px 24px;
  border: 0;
  border-radius: 0;
  box-shadow: none;
  color: #585e6a;
  cursor: pointer;
  display: inline-block;
  font-size: 1.4rem;
  font-weight: 500;
  height: auto;
  min-height: 0;
  padding: 0 22px 0 0;
  width: auto;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-header select.ui-datepicker-month {
  margin-right: 20px;
  -moz-text-align-last: right;
  text-align-last: right;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar span,
.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar a {
  font-weight: 400;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar th span {
  align-items: center;
  color: #2f4054;
  display: flex;
  font-size: 1.3rem;
  font-weight: 500;
  height: 40px;
  justify-content: center;
  width: 40px;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar td {
  font-size: 1.3rem;
  height: 50px;
  width: 50px;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar .ui-state-default {
  align-items: center;
  background: none;
  border: 1px solid transparent;
  border-radius: 100%;
  box-shadow: none;
  color: #2f4054;
  display: flex;
  height: 40px;
  justify-content: center;
  text-decoration: none;
  transition: color 300ms ease-in-out, background-color 300ms ease-in-out, border-color 300ms ease-in-out;
  width: 40px;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar .ui-state-default:hover,
.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar .ui-state-default:focus {
  background: none;
  border-color: #607382;
  outline: none;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar .ui-state-default:active {
  background: #f2f3f5;
  border-color: #607382;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar .ui-datepicker-current-day .ui-state-default {
  background: #607382;
  border-color: #607382;
  box-shadow: 0 2px 2px rgba(58, 58, 87, 0.0596411);
  color: #fff;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar .ui-datepicker-current-day .ui-state-default:hover,
.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar .ui-datepicker-current-day .ui-state-default:focus {
  border-color: #607382;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar .ui-state-disabled {
  background: none;
}

.gform-theme-datepicker:not(.gform-legacy-datepicker) .ui-datepicker-calendar .ui-state-disabled .ui-state-default {
  align-items: center;
  background: #f2f3f5;
  border: 1px solid rgba(32, 32, 46, 0.079);
  border-radius: 100%;
  box-shadow: 0 2px 2px rgba(58, 58, 87, 0.0596411);
  color: #686e77;
  cursor: text;
  display: flex;
  height: 40px;
  justify-content: center;
  text-decoration: none;
  width: 40px;
}

html[dir=rtl] #ui-datepicker-div.gform-theme-datepicker[style] {
  right: auto !important;
}

/**
   File Uploads
   */
.gform_drop_area {
  background: rgba(0, 0, 0, 0.02);
  border: 1px dashed #ccc;
  border-radius: 4px;
  margin-bottom: 8px;
  padding: 32px;
  text-align: center;
}
.gform_drop_area::before {
  content: url("../assets/images/dragandrop.svg");
  display: block;
  margin: 0 0 10px;
}
.gform_drop_area .button {
  color: var(--btn-text-color);
}
@media (hover: hover) {
  .gform_drop_area .button:hover {
    color: var(--btn-text-color--hover);
  }
}

.gform_drop_instructions {
  display: block;
  margin-bottom: 8px;
}

.gform_delete,
.gform_delete_file {
  cursor: pointer;
  vertical-align: middle;
}

.gform_delete_file {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
}

.gfield_validation_message ul {
  margin: 16px 0;
  padding-left: 24px;
}

.gfield_validation_message ul:empty {
  display: none;
}

.ginput_preview {
  margin: 16px 0;
}

.gform_fileupload_rules {
  display: block;
  margin-top: 8px;
}

.page-template-page-legal main .container {
  max-width: 60ch;
}
.page-template-page-legal .page-content {
  max-width: var(--content-max-width);
  padding-bottom: var(--block-padding);
  margin-inline: auto;
}

.aligncenter {
  display: flex;
  justify-content: center;
  margin-inline: auto;
}

@media (min-width: 992px) {
  .alignright {
    float: right;
    margin: 0 0 0 1.5em;
  }

  .alignleft {
    float: left;
    margin: 0 1.5em 0 0;
  }
}
.has-text-align-left {
  text-align: left;
}

.has-text-align-center {
  text-align: center;
}
.has-text-align-center::before, .has-text-align-center::after {
  margin-inline: auto;
}

.has-text-align-right {
  text-align: center;
}

.is-layout-flex {
  display: flex;
}

.is-content-justification-left {
  justify-content: flex-start;
}

.is-content-justification-center {
  justify-content: center;
}

.is-content-justification-right {
  justify-content: flex-end;
}

.is-content-justification-space-between {
  justify-content: space-between;
